    </el-calendar>
    
    <div class="border-2 border-gray-300"></div>
    <ul v-for="task in tasks" :key="task.id" class="w-full text-xl leading-10 list-disc px-2">
      <li class="py-5 border-b border-solid border-gray-200">
        <!-- <el-button @click="openModal(task.id)" class="float-right" type="warning">完了</el-button> -->
        <small v-if="task.done_date">{{ task.done_date }}</small>{{ task.name }}<span v-if="task.done" class="p-2 float-right border-2 border-red-500 text-gray-500 rounded-lg bg-red-200">おわり</span>
      </li>
    </ul>
    <Footer></Footer>
  </div>

</template>

<script>
// import Header from '@/components/Header.vue'
import Footer from '@/components/Footer.vue'


export default {
  data () {
    return {
      value:new Date()
    }
  },
  computed: {
    tasks () {
      return this.$store.state.tasks.filter(task => task.done_date === this.value.toLocaleDateString());
    }
  },
  components: {
    // Header,
    Footer
  }
};
</script>

